import { List } from './list';
import { Content } from './content';
// import { response } from 'express';
// import { response } from 'express';

export class Form {
  constructor(form) {
    this.form = form;
    this.inputTitle = document.querySelector('#title');
    this.inputContain = document.querySelector('#contain');
    this.closeButton = document.querySelector('#closeModal');

    this.idCounter = localStorage.getItem('id') || 0; // ÑÑ‡ÐµÑ‚Ñ‡Ð¸Ðº id

    this.noteList = document.querySelector('#noteList');
    this.list = new List(this.noteList);

    this.oneNoteContent = document.querySelector('#oneNoteContent');

    this._init();
  }

  _init() {
    this.form.addEventListener('submit', this._handleSubmit.bind(this));

    this.closeButton.addEventListener(
      'click',
      this._handleCloseModal.bind(this)
    );
  }

  _handleCloseModal() {
    this.oneNoteContent.classList.remove('underEdition'); // Ð˜Ð½Ð´Ð¸ÐºÐ°Ñ‚Ð¾Ñ€ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ñ Ð¸Ð»Ð¸ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ñ Ð·Ð°Ð¼ÐµÑ‚ÐºÐ¸
    this._resetForm(this.form);
  }

  _handleSubmit(e) {
    e.preventDefault();
    let url = `http://localhost:8080/api/data`;

    // ÐŸÐ¾Ð»ÑƒÑ‡ÐµÐ½Ð¸Ðµ Ð´Ð°Ð½Ð½Ñ‹Ñ… Ð¾ Ð²Ñ€ÐµÐ¼ÐµÐ½Ð¸
    let timeData = this._getDate();

    // ÐŸÑ€Ð¾Ð²ÐµÑ€ÐºÐ°: Ñ€ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ Ð¸Ð»Ð¸ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð·Ð°Ð¼ÐµÑ‚ÐºÐ¸
    if (!this.oneNoteContent.classList.contains('underEdition')) {
      let newNoteData = this._ÑreateNoteData(timeData, this.idCounter); // Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ Ð¾Ð±ÑŠÐµÐºÑ‚Ð°

      this._editListOfNotes(url, 'POST', newNoteData);

      localStorage.setItem('id', ++this.idCounter); // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ ÑÑ‡ÐµÑ‚Ñ‡Ð¸Ðº id
    } else {
      let noteId = localStorage.getItem('choosenNoteId');
      url = url + `/${noteId}`;

      let newNoteData = this._ÑreateNoteData(timeData, noteId); // Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ Ð¾Ð±ÑŠÐµÐºÑ‚Ð°

      this._editListOfNotes(url, 'PUT', newNoteData);

      this.oneNoteContent.classList.remove('underEdition');
    }

    this._resetForm(this.form);

    $('#formModal').modal('hide');
  }

  _resetForm(form) {
    form.reset();

    // ÐÐ°Ð¹Ð´Ñ‘Ð¼ Ð²ÑÐµ ÑÐºÑ€Ñ‹Ñ‚Ñ‹Ðµ Ð¿Ð¾Ð»Ñ Ð² Ñ„Ð¾Ñ€Ð¼Ðµ Ð¸ ÑÐ±Ñ€Ð¾ÑÐ¸Ð¼ Ð¸Ñ… Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ
    [...form.querySelectorAll('[type="hidden"]')].forEach((input) => {
      input.value = '';
    });
  }

  _parseNumber(num) {
    let parsedNum = num;

    if (parsedNum < 10) {
      return '0' + parsedNum;
    } else {
      return parsedNum;
    }
  }

  _getDate() {
    let now = new Date();
    let month = this._parseNumber(now.getMonth() + 1);
    let year = now.getFullYear();
    let day = this._parseNumber(now.getDate());

    let hours = this._parseNumber(now.getHours());
    let minutes = this._parseNumber(now.getMinutes());

    return `${day}.${month}.${year} ${hours}:${minutes}`;
  }

  // Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ Ð¸Ð»Ð¸ Ñ€ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ Ð·Ð°Ð¼ÐµÑ‚ÐºÐ¸
  _editListOfNotes(url, verb, newObj) {
    fetch(url, {
      method: verb,
      headers: { 'Content-Type': 'application/json;charset=utf-8' },
      body: JSON.stringify(newObj),
    })
      .then((response) => response.json())
      .then((data) => this.list.render(data.list))
      .catch((error) => console.error(error));
  }

  // Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ Ð·Ð°Ð¼ÐµÑ‚ÐºÐ¸
  _ÑreateNoteData(time, noteId) {
    let newNote = {};
    let formDa = new FormData(this.form);
    for (let [name, value] of formDa) {
      newNote[name] = value;
    }
    newNote.time = time;
    newNote.id = noteId;
    return newNote;
  }
}
